	module.exports = {"data":{"markdownRemark":{"html":"<h2 id=\"how-do-we-deploy\"><a href=\"#how-do-we-deploy\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>How do we deploy</h2>\n<p>An deploy starts with triggering the AuroraAPI from either of the userfacing clients <a href=\"aurora#ao\">AO</a>, <a href=\"aurora#auroraKonsole\">AuroraKonsole</a> or <a href=\"aurora#auroraPipeline\">AuroraPipeline</a>. The API will then extract and <a href=\"aurora#AuroraConfig\">merge AuroraConfig</a> in order to create a AuroraDeploymentSpec. </p>\n<p><a href=\"aurora#integrationSyncronous\">Synchrous integrations</a> are run and the result of both are assembled into Kubernetes objects that are applied to the cluster. Async integrations will that use the <a href=\"https://kubernetes.io/docs/concepts/api-extension/custom-resources/#custom-controllers\">controller pattern</a> will run and add aditional resources. The application is then rolled out either via importing a new image or triggering a new deploy. The <a href=\"aurora#deployResult\">deploy result</a> is save for later inspection.</p>","headings":[{"value":"How do we deploy","depth":2}],"fields":{"slug":"/frontpage/deploy/"},"frontmatter":{"title":""}}},"pathContext":{"slug":"/frontpage/deploy/"}}

